
'use client';

import { memo, useEffect, useMemo, useRef, useState } from 'react';
import {
  MapContainer,
  TileLayer,
  Marker,
  Popup,
  useMap,
  useMapEvents,
} from 'react-leaflet';
import L, { Map as LeafletMap } from 'leaflet';
import 'leaflet/dist/leaflet.css';
import { useTrackingStore } from '@/store/tracking-store';
import type { Vehicle } from '@/lib/types';
import {
  ContextMenu,
  ContextMenuContent,
  ContextMenuItem,
  ContextMenuTrigger,
} from "@/components/ui/context-menu"
import { CornerDownLeft, CornerUpRight, MapPin } from 'lucide-react';
import { useDispatchStore } from '@/store/dispatch-store';
import { useToast } from '@/hooks/use-toast';
import { useRouter } from 'next/navigation';

// Fix for default marker icons not appearing in Next.js
delete (L.Icon.Default.prototype as any)._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon-2x.png',
  iconUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon.png',
  shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-shadow.png',
});


/* ================================
   Imperative View Controller
================================ */
function ChangeView({
  center,
  zoom,
}: {
  center: [number, number];
  zoom: number;
}) {
  const map = useMap();

  useEffect(() => {
    map.setView(center, zoom, { animate: true });
  }, [center, zoom, map]);

  return null;
}

/* ================================
   Map Context Menu Handler
================================ */
function MapContextMenu() {
    const { setPickup, setDropoff } = useDispatchStore();
    const { toast } = useToast();
    const router = useRouter();
    const [contextMenuPos, setContextMenuPos] = useState<L.LatLng | null>(null);

    const map = useMapEvents({
        contextmenu(e) {
            setContextMenuPos(e.latlng);
        },
    });

    const handleSetLocation = (type: 'pickup' | 'dropoff') => {
        if (!contextMenuPos) return;

        // In a real app, you would use a reverse geocoding service here.
        // For now, we'll just use the coordinates as a string.
        const address = `${contextMenuPos.lat.toFixed(4)}, ${contextMenuPos.lng.toFixed(4)}`;

        if (type === 'pickup') {
            setPickup(address);
            toast({
                title: "Pickup Location Set",
                description: `Address: ${address}`,
                action: (
                    <button onClick={() => router.push('/dashboard/dispatch')} className="text-sm font-semibold hover:underline">
                        Go to Dispatch
                    </button>
                )
            });
        } else {
            setDropoff(address);
             toast({
                title: "Dropoff Location Set",
                description: `Address: ${address}`,
                 action: (
                    <button onClick={() => router.push('/dashboard/dispatch')} className="text-sm font-semibold hover:underline">
                        Go to Dispatch
                    </button>
                )
            });
        }
        setContextMenuPos(null); // Close menu
    };

    if (!contextMenuPos) return null;

    // We can't use ShadCN's ContextMenuTrigger here because we need to control it from the map event.
    // Instead, we manually render the ContextMenuContent at the clicked position.
    // This is a simplified approach. A more robust one might involve a custom portal.
    return (
        <div style={{ position: 'absolute', left: `${map.latLngToContainerPoint(contextMenuPos).x}px`, top: `${map.latLngToContainerPoint(contextMenuPos).y}px`, zIndex: 1000 }}>
             <ContextMenu open={true} onOpenChange={() => setContextMenuPos(null)}>
                <ContextMenuContent>
                    <ContextMenuItem onSelect={() => handleSetLocation('pickup')}>
                        <CornerUpRight className="mr-2 h-4 w-4" />
                        Set as Pickup Location
                    </ContextMenuItem>
                    <ContextMenuItem onSelect={() => handleSetLocation('dropoff')}>
                        <CornerDownLeft className="mr-2 h-4 w-4" />
                        Set as Dropoff Location
                    </ContextMenuItem>
                </ContextMenuContent>
            </ContextMenu>
        </div>
    );
}

/* ================================
   Stable Icon Factory
================================ */
const useVehicleIcons = () =>
  useMemo(() => {
    const base = {
      iconSize: [28, 41] as L.PointExpression,
      iconAnchor: [14, 41] as L.PointExpression,
      popupAnchor: [0, -41] as L.PointExpression,
      shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-shadow.png',
      shadowSize: [41, 41] as L.PointExpression,
    };

    return {
      ON_TRIP: new L.Icon({ ...base, iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-blue.png' }),
      AVAILABLE: new L.Icon({ ...base, iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-green.png' }),
      MAINTENANCE: new L.Icon({ ...base, iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-orange.png' }),
      ASSIGNED: new L.Icon({ ...base, iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-violet.png' }),
      OUT_OF_SERVICE: new L.Icon({ ...base, iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-red.png' }),
      SELECTED: new L.Icon({ ...base, iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-black.png' }),
    };
  }, []);


/* ================================
   Main Map Component (Fast Refresh Safe)
================================ */
function LeafletTrackingMap({
  vehicles,
  center,
  zoom = 13,
}: {
  vehicles: Vehicle[];
  center: [number, number];
  zoom?: number;
}) {
  const mapRef = useRef<LeafletMap | null>(null);
  const { selectedVehicle, selectVehicle } = useTrackingStore();
  const icons = useVehicleIcons();

  /* üî• CRITICAL FIX: destroy map on unmount */
  useEffect(() => {
    return () => {
      if (mapRef.current) {
        mapRef.current.remove(); // ‚Üê THIS prevents re-init error forever
        mapRef.current = null;
      }
    };
  }, []);

  return (
    <MapContainer
      center={center}
      zoom={zoom}
      style={{ height: '100%', width: '100%' }}
      scrollWheelZoom
      preferCanvas
      whenCreated={(map) => {
        mapRef.current = map;
        // Position zoom control to the bottom left
        map.zoomControl.setPosition('bottomleft');
      }}
      zoomControl={true} // Explicitly enable zoom control
      contextmenu={true} // Enable context menu events
    >
      {/* IMPORTANT: Change view without rerendering MapContainer */}
      <ChangeView center={center} zoom={zoom} />

      <TileLayer
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        attribution="&copy; OpenStreetMap contributors"
      />

      <MapContextMenu />

      {vehicles.map((v) => {
        const isSelected = selectedVehicle?.id === v.id;
        const statusKey = v.status as keyof ReturnType<typeof useVehicleIcons>;
        const icon = isSelected ? icons.SELECTED : (icons[statusKey] || icons.AVAILABLE);
        
        return (
          <Marker
            key={v.id}
            position={[v.currentLocation.lat, v.currentLocation.lng]}
            icon={icon}
            eventHandlers={{
              click: () => {
                selectVehicle(v);
              },
            }}
          >
            <Popup>
              <div>
                <strong>{v.plateNumber}</strong>
                <br />
                Status: {v.status}
              </div>
            </Popup>
          </Marker>
        );
      })}
    </MapContainer>
  );
}

export default memo(LeafletTrackingMap);
